@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: 'Poppins';
  src: url('./fonts/Poppins-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'SourGummy';
  src: url('./fonts/SourGummy-VariableFont_wdth,wght.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'StyleScript';
  src: url('./fonts/StyleScript-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'PlayfairDisplay';
  src: url('./fonts/PlayfairDisplay-VariableFont_wght.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

.SourGummy {
  font-family: 'SourGummy';
}
.StyleScript{
  font-family: 'StyleScript';
}

.Poppins{
  font-family: 'Poppins';
}
body {
  font-family: 'Poppins', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Custom scrollbar styles */
.categories-container::-webkit-scrollbar {
  height: 5px;
  /* Height of the horizontal scrollbar */
}

.categories-container::-webkit-scrollbar-thumb {
  background-color: #ffffff;
  /* Color of the scrollbar thumb */
  border-radius: 10px;
  /* Rounded corners for the scrollbar thumb */
}

.categories-container::-webkit-scrollbar-track {
  background-color: #00e1ff;
  /* Color of the scrollbar track */
}

.Uppermain-bgcolor {
  background: rgb(212, 245, 255);
  background: linear-gradient(90deg, rgba(212, 245, 255, 1) 6%, rgba(94, 214, 234, 1) 6%, rgba(0, 189, 218, 1) 14%, rgba(212, 245, 255, 1) 14%, rgba(223, 248, 251, 1) 21%, rgba(0, 189, 218, 1) 21%, rgba(64, 206, 228, 1) 28%, rgba(223, 248, 251, 1) 28%, rgba(255, 255, 240, 1) 37%, rgba(0, 189, 218, 1) 37%, rgba(0, 189, 218, 1) 45%, rgba(174, 242, 255, 1) 45%, rgba(212, 245, 255, 1) 54%, rgba(0, 189, 218, 1) 54%, rgba(0, 189, 218, 1) 63%, rgba(212, 245, 255, 1) 63%, rgba(212, 245, 255, 1) 71%, rgba(0, 189, 218, 1) 71%, rgba(0, 189, 218, 1) 79%, rgba(195, 247, 255, 1) 79%, rgba(179, 245, 255, 1) 87%, rgba(0, 189, 218, 1) 87%, rgba(0, 189, 218, 1) 95%, rgba(217, 250, 255, 1) 95%);
}

.Uppermain-bgcolor-dark {
  background: linear-gradient(
    90deg,
    rgba(20, 30, 40, 1) 6%,       /* Dark slate */
    rgba(30, 60, 70, 1) 6%,       /* Teal-ish dark */
    rgba(10, 40, 60, 1) 14%,      /* Deep blue-teal */
    rgba(20, 30, 40, 1) 14%,      /* Dark slate */
    rgba(25, 50, 65, 1) 21%,      /* Muted blue */
    rgba(10, 40, 60, 1) 21%,      /* Deep blue-teal */
    rgba(15, 45, 70, 1) 28%,      /* Slightly brighter blue */
    rgba(25, 50, 65, 1) 28%,      /* Muted blue */
    rgba(40, 40, 40, 1) 37%,      /* Almost black gray */
    rgba(10, 40, 60, 1) 37%,      /* Deep blue-teal */
    rgba(10, 40, 60, 1) 45%,      /* Deep blue-teal */
    rgba(35, 65, 85, 1) 45%,      /* Cool dark cyan */
    rgba(20, 30, 40, 1) 54%,      /* Dark slate */
    rgba(10, 40, 60, 1) 54%,      /* Deep blue-teal */
    rgba(10, 40, 60, 1) 63%,      /* Deep blue-teal */
    rgba(20, 30, 40, 1) 63%,      /* Dark slate */
    rgba(20, 30, 40, 1) 71%,      /* Dark slate */
    rgba(10, 40, 60, 1) 71%,      /* Deep blue-teal */
    rgba(10, 40, 60, 1) 79%,      /* Deep blue-teal */
    rgba(30, 60, 70, 1) 79%,      /* Teal-ish dark */
    rgba(25, 50, 65, 1) 87%,      /* Muted blue */
    rgba(10, 40, 60, 1) 87%,      /* Deep blue-teal */
    rgba(10, 40, 60, 1) 95%,      /* Deep blue-teal */
    rgba(35, 65, 85, 1) 95%       /* Cool dark cyan */
  );
}

.shadowX {
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}